import ProductsList from "../components/productsList/productsList";
import ButtonFeedback from "../components/buttonFeedback/buttonFeedback";
import Location from "../components/location/Location";
import { useLocation } from "react-router-dom";

const ProductsListPage = () => {
    const location = useLocation();
    const locationLinks = [ 
        { link: "/schreimaneng_ineering", text: ' /' }, 
        { link: "/schreimaneng_ineering/catalog", text: '  /' },
    ];

    const findLocation = () => {
        switch (location.pathname) {
            case '/schreimaneng_ineering/catalog/controlPanel': {
                return locationLinks.push({ link: "/schreimaneng_ineering/catalog/controlPanel", text: '  ' });
            }
            case '/schreimaneng_ineering/catalog/autoDevice': {
                return locationLinks.push({ link: "/schreimaneng_ineering/catalog/autoDevice", text: '  ' });
            }
            case '/schreimaneng_ineering/catalog/regulator': {
                return locationLinks.push({ link: "/schreimaneng_ineering/catalog/regulator", text: ' ' });
            }
            case '/schreimaneng_ineering/catalog/mixUnits': {
                return locationLinks.push({ link: "/schreimaneng_ineering/catalog/mixUnits", text: '  ' });
            }
        }
    }

    findLocation();

    return (
        <>
            <Location path={locationLinks} />
            <ProductsList />
            <ButtonFeedback />
        </>
    )
}

export default ProductsListPage;