import Styles from './Payment.module.css';

const Payment = () => {
    return (
        <div className={Styles.payment}>
            <h3 className={Styles.payment__title} >  </h3>
            <div className={Styles.payment__textBlock}>
                <p className={Styles.payment__text} >   3 :</p>
                <ol className={Styles.payment__list} >
                    <li className={Styles.payment__text} > </li>
                    <li className={Styles.payment__text} ></li>
                    <li className={Styles.payment__text} >    . </li>
                </ol>
                <p className={Styles.payment__text} >      .</p>
                <p className={Styles.payment__text} >     500.</p>
            </div>
        </div>
    )
};

export default Payment;